const currencySigns: { [key: string]: string } = {
  btc: "",
  eth: "",
  ltc: "",
  xrp: "XRP",
  usd: "$",
  aed: "AED",
  aud: "A$",
  bdt: "",
  bhd: "..",
  brl: "R$",
  cad: "CA$",
  chf: "CHF",
  cny: "",
  czk: "K",
  dkk: "kr",
  eur: "",
  gbp: "",
  hkd: "HK$",
  huf: "ft",
  idr: "Rp",
  ils: "",
  inr: "",
  rub: "",
  jpy: "",
};

export default (currency: string): string => {
  if (currencySigns.hasOwnProperty(currency)) {
    return currencySigns[currency];
  }
  return "";
};
